<script>
import { mapState } from 'vuex'
import { mixin as clickaway } from 'vue-clickaway';

export default {
  mixins: [ clickaway ],
  data: function() {
    return {
      showUserMenu: false
    }
  },
  methods: {
    async logout() {
      try {
        await this.$store.dispatch('auth/logout')
        this.$router.push('/')
      } catch (e) {
        console.log(e)
      }
    },
    clickedOutsideUserMenu() {
      this.showUserMenu = false
    }
  },
  computed: {
    ...mapState({
      me: state => state.me.me
    })
  },
  created() {
    this.$store.dispatch('me/fetch')
  }
}
</script>

