<script>

export default {

  data: function() {
    return {
      success: null,
      info: null,
      error: null,
      registering: false,
      accountCreated: false,
      authenticating: false
    }
  },
  metaInfo: {
    title: 'Sign up | Reviewgrower'
  },
  methods: {
    async submit() {

      this.registering = true
      this.error = null

      try {
        //await this["auth/signup"]({ first_name: this.first_name, last_name: this.last_name, email: this.email, password: this.password })
        // android/ios chrome autofill fix (access input values directly)
        await this.$store.dispatch("auth/signup", { company_name: this.$refs.company_name.value.trim(), company_type: this.$refs.company_type.value.trim(), first_name: this.$refs.first_name.value.trim(), last_name: this.$refs.last_name.value.trim(), email: this.$refs.email.value.trim(), password: this.$refs.password.value.trim() })
        this.accountCreated = true
      }catch(error){
        if (typeof(error) == "undefined") {
          this.accountCreated = true
        }
        else {
          console.log(error)
          for (var i in error.errors) {
            if (error.errors[i] != 'null') {
              console.log(error.errors[i]);
              this.error = (this.error || '') + error.errors[i] + "<br />";
            }
          }
        }
      }

      this.registering = false

    }
  }
}
</script>



