<template>

  <panel-navigation>
    
    <h1 class="text-lg font-semibold mb-4">User Settings</h1>
    <hr class="border-b border-gray-100 mb-4">

    <div v-if="me">


    <div class="mb-6 rounded-lg bg-super-light p-6 border-2 border-gray-200">
      <div class="mb-3">
        <h2 class="font-bold text-gray-700">Profile</h2>
        <hr class="border-b border-gray-100">
      </div>

      <div class="flex flew-row flex-wrap">
        <div class="w-full sm:w-1/3 pr-6 mb-4">
          <p class="mb-1 text-sm mb-2">First Name</p>
          <input type="text" class="block appearance-none focus:outline-none focus:border-gray-500 w-full bg-white border-2 border-grey-light hover:border-grey px-2 py-2 rounded" v-model="me.first_name" />
        </div>
        <div class="w-full sm:w-1/3 pr-6 mb-4">
          <p class="mb-1 text-sm mb-2">Last Name</p>
          <input type="text" class="block appearance-none focus:outline-none focus:border-gray-500 w-full bg-white border-2 border-grey-light hover:border-grey px-2 py-2 rounded" v-model="me.last_name" />
        </div>
      </div>

      <div class="w-full sm:w-1/3 pr-6 mb-4">
        <p class="mb-1 text-sm mb-2">Email</p>
        <div type="text" class="block w-full bg-white border-2 border-grey-light hover:border-grey px-2 py-2 rounded opacity-50 select-none">{{me.email}}</div>
      </div>

    </div>


    <div>
      <div class="bg-green-500 hover:bg-green-400 text-white inline-block rounded py-2 px-3 cursor-pointer" :class="{'spinner': saving}" @click="save">Save</div>
    </div>

    </div>



  </panel-navigation>

</template>

<script>
import { mapState } from 'vuex'
import PanelNavigation from '@/components/PanelNavigation.vue'

export default {
  metaInfo: {
    title: 'User Settings | Reviewgrower'
  },
  components: {
    PanelNavigation
  },
  data() {
    return {
      userSegmentation: false,
      saving: false,
      error: null
    }
  },
  methods: {
    async save() {

      this.saving = true
      this.error = null

      try {
        await this.$store.dispatch('me/save', {
          first_name: this.me.first_name.trim(),
          last_name: this.me.last_name.trim()
        })
        this.$store.dispatch('me/fetch')
      }catch(error){
        console.log(error)
        this.error = error
      }

      this.saving = false

    }
  },
  computed: {
    ...mapState({
      me: state => state.me.me
    })
  },
  created() {
    this.$store.dispatch('me/fetch')
  }
}
</script>

<style scoped>
.setting-header {
  background-color: #fbfbfb;
}
.setting-body {
  background-color: #fdfdfd;
}
</style>

