<template>
  <div>

    <v-skeleton-loader v-if="initial_loading"
      type="article, article, article"
    ></v-skeleton-loader>

    <div class="w-full mb-4" v-if="!initial_loading">
        <div v-if="dashboard == 'true'" class="bg-gray-200 px-3 py-2 rounded-t flex flex-row items-center justify-between">
          <div>Recent Reviews</div>
        </div>
        <v-card
          class="pa-2"
          outlined
          tile
        >

        <v-data-table
            :headers="headers"
            :items="reviews"
            :options.sync="options"
            :server-items-length="totalFilteredReviews"
            :items-per-page="5"
            class="elevation-1"
            :loading="loading_reviews"
            :footer-props="{
            itemsPerPageOptions: [5, 10, 20, 30, 40, 50, 100],
            showFirstLastPage: true
            }"
            :hide-default-footer="dashboard == 'true'"
        >

        <template v-slot:header>
          <v-toolbar
              dark
              color="green lighten-1"
              class="mb-1"

              v-if="dashboard == 'false'"
          >
              <v-row>
              <v-col cols="12" md="3" sm="3" xs="6">
                  <v-text-field
                  v-model="searchLocal"
                  clearable
                  flat
                  solo-inverted
                  hide-details
                  prepend-inner-icon="mdi-magnify"
                  label="Search"
                  ></v-text-field>
              </v-col>
              <template v-if="$vuetify.breakpoint.smAndUp">
                  <v-col cols="12" md="3" sm="3" xs="6">
                  <v-select
                      v-model="sortByLocal"
                      flat
                      solo-inverted
                      hide-details
                      :items="sort_by_keys"
                      prepend-inner-icon="mdi-sort"
                      label="Sort by"
                  ></v-select>
                  </v-col>
                  <v-spacer></v-spacer>
                  <v-col cols="12" md="3" sm="3" xs="6">
                  <v-select
                      v-model="filterRatingLocal"
                      clearable
                      flat
                      solo-inverted
                      hide-details
                      :items="filter_rating_keys"
                      prepend-inner-icon="mdi-hexagram"
                      label="Rating"
                  ></v-select>
                  </v-col>
                  <v-spacer></v-spacer>
                  <v-col cols="12" md="3" sm="3" xs="6">
                  <v-select
                      v-model="filterWebsiteLocal"
                      clearable
                      flat
                      solo-inverted
                      hide-details
                      :items="filter_website_keys"
                      prepend-inner-icon="mdi-earth"
                      label="Any Website"
                      item-text="name"
                      item-value="domain"
                  ></v-select>
                  </v-col>
              </template>
              </v-row>
          </v-toolbar>
          </template>
            <template v-slot:item.url="{ item }">
            <span v-if="item">{{ getHostname(item) }}</span>
            </template>

            <template v-slot:item.rating_value="{ item }">
            <v-rating
                :value="item.rating_value"
                color="amber"
                dense
                half-increments
                readonly
                size="20"
            ></v-rating>
            </template>
            <template v-slot:item.review_text="{ item }">
            <div class="reviewer">
                <!-- <img class="profile-pic" :src="item.profile_picture" v-if="item.profile_picture" /> -->

                <v-avatar color="blue" size="30" v-if="!item.profile_picture || 1"><v-icon small color="white">mdi-account</v-icon></v-avatar>
                <h3>{{ item.name }}</h3>

                <v-rating
                :value="item.rating_value"
                color="amber"
                dense
                half-increments
                readonly
                size="20"
                ></v-rating>

                <v-menu offset-y>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      v-bind="attrs"
                      v-on="on"
                      class="manage-btn mt-1"
                      small
                      icon
                    ><i class="fa fa-cog" style="font-size: 18px;"></i></v-btn>
                  </template>
                  <v-list>
                    <v-list-item
                      v-for="(item2, index) in review_manage_items"
                      :key="index"

                      @click="selectReviewItem(index, item.id)"
                    >
                      <v-list-item-title>{{ item2.title }}</v-list-item-title>
                    </v-list-item>
                  </v-list>
                </v-menu>

                <v-chip
                class="ma-2"
                filter
                small
                >
                {{ formatDate(item.date) }}
                </v-chip>

                <v-chip
                class="ma-2"
                filter
                small
                color="blue"
                text-color="white"
                >

                {{ getHostname(item) }}
                </v-chip>
                <img class="source-logo" :src="require('../assets/images/review-services/www.' + getHostname(item) + '.png')">
            </div>

            <em>
                <v-expansion-panels>
                <v-expansion-panel>
                    <v-expansion-panel-header>
                    {{ displayExpansionTitle(item) }}
                    </v-expansion-panel-header>
                    <v-expansion-panel-content>
                    <div v-html='item.review_text' />
                    </v-expansion-panel-content>
                </v-expansion-panel>
                </v-expansion-panels>
            </em>
            </template>
        </v-data-table>

        </v-card>
    </div>

  </div>
</template>

<script>
  import { mapState } from 'vuex'
  import BusinessesAPI from '@/plugins/api/endpoints/businesses.js';
  import PanelNavigation from '@/components/PanelNavigation.vue'
  import moment from 'moment'

  import reviewSites from '@/config/review_sites/list';

  export default {
    components: {
        PanelNavigation
    },
    props: ['dashboard', 'sortBy', 'sortDesc', 'page', 'itemsPerPage', 'search', 'filter_rating', 'filter_website'],
    data () {
      return {
        options: {
          sortBy: "Newest First",
          sortDesc: true
        },
        headers: [
          // { text: 'Reviewer', value: 'name' },
          // { text: 'Date', value: 'date', width: 150 },
          // { text: 'Site', value: 'url', width: 150 },
          // { text: 'Rating', value: 'rating_value' },
          { text: 'Reviews', value: 'review_text', html: true, sortable: false }
        ],
        read_more: [],
        percentage_complete: 0,
        sort_by_keys: [
          'Newest First',
          'Oldest First'
        ],
        filter_rating_keys: [
          "1",
          "2",
          "3",
          "4",
          "5",
          "Positive",
          "Negative"
        ],
        filter_website_keys: reviewSites,
        searchLocal: "",
        sortByLocal: "Newest First",
        filterRatingLocal: "",
        filterWebsiteLocal: "",
        review_manage_items: [
          { title: 'Social Post' }
        ]
      }
    },
    computed: {
      ...mapState({
        reviews: state => state.reviews.reviews,
        loading_reviews: state => state.reviews.loading_reviews,
        initial_loading: state => state.reviews.initial_loading,
        totalFilteredReviews: state => state.reviews.totalFilteredReviews,
        totalReviews: state => state.reviews.totalReviews
      })
    },
    methods: {
      getReviews: function(ignore_loading) {

        const { sortBy, sortDesc, page, itemsPerPage } = this.options

        this.$store.dispatch('reviews/fetch', {
          dashboard: this.dashboard,
          sortBy: sortBy,
          sortDesc: sortDesc,
          page: page,
          itemsPerPage: itemsPerPage,
          search: this.searchLocal || "",
          filter_rating: this.filterRatingLocal || "",
          filter_website: this.filterWebsiteLocal || ""
        });

        return;
      },
      getHostname: function(item) {
        return new URL(item.url || item.listing.url).hostname.replace("www.", "");
      },
      formatDate: function(date) {
        return moment(String(date)).format('MM/DD/YYYY')
      },
      displayExpansionTitle(item) {
        if (item.review_title)
          return item.review_title;

        if (item.review_text)
          return item.review_text.substring(0, 50) + '...';

        return 'No text';
      },
      selectReviewItem(item, review_id) {
        switch (item) {
          case 0: // Social Post
            this.$router.push('social-share/' + review_id)
          break;
        }
      }
    },
    watch: {
      options: {
        handler () {
          this.getReviews()
        }
      },
      sortByLocal: {
        handler () {
          if (this.sortByLocal == 'Newest First') {
              this.options.sortBy = "date";
              this.options.sortDesc = true;
            }
            else if (this.sortByLocal == 'Oldest First') {
              this.options.sortBy = "date";
              this.options.sortDesc = false;
            }
            this.getReviews()
        }
      },
      searchLocal: {
        handler () {
          this.options.page = 1
          this.getReviews()
        }
      },
      filterRatingLocal: {
        handler () {
          this.options.page = 1
          this.getReviews()
        }
      },
      filterWebsiteLocal: {
        handler () {
          this.options.page = 1
          this.getReviews()
        }
      }
    },
    mounted () {
      var that = this
      this.$store.state.reviews.initial_loading = true
      setTimeout(function() {
        that.getReviews()
      }, 1000)
    },
  }
</script>

