<template>
  <div class="bg-gray-300 min-h-screen font-sans">

    <div class="container mx-auto min-h-screen flex justify-center items-top px-3">
        <div class="w-full lg:w-1/3 pb-24 pt-12" style="">
              
            <div class="flex flex-col items-center">
              <router-link to="/">
                <img src="@/assets/icon-gray3.png" alt="" style="max-width: 120px;" class="mb-12"> 
              </router-link> 
            </div>

            <div class="border-teal p-8 border-t-12 bg-white mb-6 rounded-lg shadow-lg">

                <h1 class="text-2xl mb-6 text-center font-semibold text-gray-700">Log in to Reviewgrower</h1>

                <div v-if="success" class="bg-green-100 border-l-4 border-green-500 text-green-700 p-4 mb-4" role="alert">
                  {{success}}
                </div>

                <div v-if="error" class="bg-red-100 border-l-4 border-red-500 text-red-700 p-4 mb-4" role="alert" v-html="error">
                </div>

                <div class="mb-4">
                    <input type="email" ref="email" class="block appearance-none focus:outline-none focus:border-gray-500 w-full bg-white border-2 border-grey-light hover:border-grey px-2 py-2 rounded" placeholder="Email Address" :class="{'opacity-50 cursor-not-allowed': authenticating}" @keyup.enter="$refs.password.focus">
                </div>

                <div class="mb-2">
                    <input type="password" ref="password" class="block appearance-none focus:outline-none focus:border-gray-500 w-full bg-white border-2 border-grey-light hover:border-grey px-2 py-2 rounded" placeholder="Password" :class="{'opacity-50 cursor-not-allowed': authenticating}" v-on:keyup.enter="submit">
                </div>

                <div class="mb-6">
                  <a class="no-underline align-baseline font-medium text-sm text-gray-600 hover:text-gray-700" href="#">
                      Forgot your password?
                  </a>
                </div>


                <div class="">

                    <button class="w-full rounded bg-green-500 hover:bg-green-400 hover:border-green-500 text-white focus:outline-none py-2 px-4" :class="{'spinner': authenticating}" @click="submit">
                        Login
                    </button>

                </div>
                
            </div>
            <div class="text-center">
                <p class="text-gray-600 text-sm">Don't have an account? <router-link to="/signup" class="no-underline text-blue font-bold">Create an Account</router-link>.</p>
            </div>
        </div>
    </div>

  </div>

</template>



<script>

import axios from 'axios'
export default {

  data: function() {
    return {
      success: null,
      info: null,
      error: null,
      authenticating: false,
      accountCreated: false,
      needsEmailVerification: false
    }
  },
  metaInfo: {
    title: 'Login | Reviewgrower'
  },
  methods: {
    async submit() {

      this.authenticating = true
      this.error = null
      this.success = null

      try {
        await this.$store.dispatch("auth/login", { email: this.$refs.email.value.trim(), password: this.$refs.password.value.trim() })
        this.$router.push("/activity");

      }catch(error){
        console.log(error)
        this.error = 'Given credentials are not valid.'
      }

      this.authenticating = false

    }
  },
  created() {
    if(this.$route.query.verified) {
      this.success = 'Thank you for confirming your email. You can log in now.'
      this.$router.push('login')
    }
  },
  mounted() {
    this.$refs.email.focus()
  }
};
</script>

<style lang="scss" scoped>
.signup {
  h1 {
    margin-top: 20px;
    margin-bottom: 20px;
  }
}

input, select {
  border-style: solid;
}
</style>
