<script>
import axios from 'axios'
import moment from 'moment'

export default {
    data () {
      return {
        dialog: false,
        notifications: false,
        sound: true,
        widgets: false,
        widget_data: false,
        scroll_working: false,
        current_visible: 0,
        show_rating: false,
        reviews_start: 0,
        finishedLoadingReviews: false
      }
    },
    computed: {
        visibleReviews () {
            return this.widget_data.reviews.filter(p=>p.isActive).length
        }
    },
    methods: {
        showDialog: function() {
            let self = this
            this.dialog = true
            setTimeout(function() {
                self.show_rating = true
            }, 100);

            parent.postMessage("showReviews","*");  //  `*` on any domain  
        },
        closeDialog: function() {
            this.dialog = false, this.show_rating = false, parent.postMessage('closeReviews','*')
        },
        getWidgetData: async function() {
            let self = this
            try {
                let url = process.env.VUE_APP_API_ENDPOINT + '/widgets/get-data/' + this.$route.params.token;

                // Preview
                let exploded = window.location.href.split("?fw");
                if (exploded.length > 1) {
                    url = url + "?fw=" + exploded[1];
                }
                
                let response = await axios.get(url, {

                } , {
                    headers: {
                        'Authorization': 'Bearer ' + this.$store.state.auth.jwt
                    },
                })
                
                this.widget_data = response.data.widget_data

                this.widget_data.displayed_reviews = [];

                for (var i in this.widget_data.reviews) {
                    if (i < 25) {
                        this.widget_data.displayed_reviews.push(this.widget_data.reviews[i]);
                        self.current_visible++;
                    }
                }
            }catch(e){
                console.log(e)
            }
        },
        fetchMoreReviews: async function() {
            let self = this
            self.reviews_start = self.reviews_start + 20;
            try {
                let url = process.env.VUE_APP_API_ENDPOINT + '/widgets/get-data/' + this.$route.params.token + "?start=" + self.reviews_start;

                // Preview
                let exploded = window.location.href.split("?fw");
                if (exploded.length > 1) {
                    url = url + "&fw=" + exploded[1];
                }
                
                let response = await axios.get(url, {

                } , {
                    headers: {
                        'Authorization': 'Bearer ' + this.$store.state.auth.jwt
                    },
                })
                
                if (response.data.widget_data.reviews.length == 0) {
                    self.finishedLoadingReviews = true;
                }
                else {
                    for (var i in response.data.widget_data.reviews) {
                        this.widget_data.reviews.push(response.data.widget_data.reviews[i]);
                    }
                }
            }catch(e){
                console.log(e)
            }
        },
        getHostname: function(item) {
            return new URL(item.listing.url).hostname.replace("www.", "");
        },
        formatDate: function(date) {
            return moment(String(date)).format('MM/DD/YYYY')
        },
        onScroll: function(e) {
            // let self = this;

            // if (!self.scroll_working) {
            //     console.log(e.target.scrollHeight)
            //     console.log(e.target.scrollTop + e.target.offsetHeight)
            //     console.log(1);
            //     if (e.target.scrollHeight - (e.target.scrollTop + e.target.offsetHeight) < 100) {
            //         self.scroll_working = true;

            //         console.log(2);
            //         if (self.current_visible < self.widget_data.total_reviews) {
