            //                 if (typeof(self.widget_data.reviews[self.current_visible]) != "undefined") {
            //                     this.widget_data.displayed_reviews.push(this.widget_data.reviews[self.current_visible]);
            //                     self.current_visible++;
            //                 }
            //             }
            //         }
            //     }
            // }

            // setTimeout(function() {
            //     self.scroll_working = false;
            // }, 4000);
        }
    },
    created() {
        this.getWidgetData();
    }
}
